<template>
    <b-container>
        <b-list-group v-for="group of groups" v-bind:key="group.category" class="gl">
            <b-list-group-item v-for="(item, index) of group.items" v-bind:key="index">
                <b-row>
                    <b-col>
                        <p class="cl-d" v-html="item.description"></p>
                    </b-col>
                    <b-col sm="*">
                        <h2 class="cl-p"><span>{{ `${group.category}.${index + 1}` }}</span></h2>
                    </b-col>
                </b-row>
            </b-list-group-item>
        </b-list-group>
    </b-container>
</template>
<script>
function acquire(mutation, state) {
    if (mutation.type === "update")
        this.$set(this, "groups", state.groups);
}


export default {
    data: () => (
        {
            groups: []
        }
    ),

    mounted() {
        if (this.$store.state.groups.length)
            acquire.call(this, { type: "update", payload: null }, this.$store.state);

        this.$store.subscribe(acquire.bind(this));
    }
};
</script>
<style scoped>
p {
    margin: 0;
}

.gl {
    margin: 1em 0;
}

.cl-p {
    font-size: 1em;
    margin: 0 0.2em;

    display: flex;
    height: 100%;

    align-items: center;
}

.cl-d {
    font-size: 1.2em;
    text-indent: 1em;
    text-align: left;

    word-break: break-word;
}

.cl-p, .cl-d {
    color: #2f2f2f;
}
</style>

