<template>
    <div id="app">
        <b-navbar>
            <div class="bd-5 d-flex w-100">
                <div class="d-flex justify-content-center w-100">
                    <b-navbar-nav>
                        <b-nav-item><router-link to="/">Главная</router-link></b-nav-item>
                        <b-nav-item><router-link to="/sheet">Список</router-link></b-nav-item>
                    </b-navbar-nav>
                </div>
            </div>
        </b-navbar>

        <router-view></router-view>
    </div>
</template>

<script>
import Vue from "vue";

import VueRouter from "vue-router";
Vue.use(VueRouter);

import Vuex from "vuex";
Vue.use(Vuex);

import InformationHome from "./components/routered/InformationHome.vue";
import InformationSheet from "./components/routered/InformationSheet.vue";

export default {
    name: "App",
    router: new VueRouter(
        {
            mode: "history",
 
            routes: [
                {
                    path: "/",
                    component: InformationHome
                },
                {
                    path: "/sheet",
                    component: InformationSheet
                }
            ]
        }
    ),
    store: new Vuex.Store(
        {
            state: {
                groups: []
            },

            actions: {
                update(store, url) {
                    fetch(url).then(
                        async data => {
                            store.commit('update', await data.json());
                        }
                    );
                }
            },

            mutations: {
                update(state, groups) {
                    state.groups = groups;
                }
            }
        }
    ),

    created() {
        this.$store.dispatch("update", "/data.json");
    }
};
</script>
